import logo from '../../components/logo.vue';
import mInput from '../../components/m-input.vue';
import {
	login
} from '../../api/api.js';
import {
	joinValidate,
	pwdValidate
} from '../../common/js/validate.js';

export default {
	components: {
		mInput,
		logo
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0
		}
	},
	methods: {
		bindLogin() {
			// console.log(result);
			if (joinValidate(this.account) && pwdValidate(this.password, this.password)) {
				login(this.account, this.password);
			}

		}
	}
}
